/* Magnific Popup CSS */

.mfp-bg {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1042;
  overflow: hidden;
  position: fixed;
  background: #0b0b0b;
  opacity: 0.8;
}

.mfp-wrap {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1043;
  position: fixed;
  outline: none !important;
  -webkit-backface-visibility: hidden;
}

.mfp-container {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding: 0 8px;
  box-sizing: border-box;
}

.mfp-container:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.mfp-align-top .mfp-container:before {
  display: none;
}

.mfp-content {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  text-align: left;
  z-index: 1045;
}

.mfp-inline-holder .mfp-content,
.mfp-ajax-holder .mfp-content {
  width: 100%;
  cursor: auto;
}

.mfp-ajax-cur {
  cursor: progress;
}

.mfp-zoom-out-cur,
.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: -moz-zoom-out;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}

.mfp-zoom {
  cursor: pointer;
  cursor: -webkit-zoom-in;
  cursor: -moz-zoom-in;
  cursor: zoom-in;
}

.mfp-auto-cursor .mfp-content {
  cursor: auto;
}

.mfp-close,
.mfp-arrow,
.mfp-preloader,
.mfp-counter {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.mfp-loading.mfp-figure {
  display: none;
}

.mfp-hide {
  display: none !important;
}

.mfp-preloader {
  color: #CCC;
  position: absolute;
  top: 50%;
  width: auto;
  text-align: center;
  margin-top: -0.8em;
  left: 8px;
  right: 8px;
  z-index: 1044;
}

.mfp-preloader a {
  color: #CCC;
}

.mfp-preloader a:hover {
  color: #FFF;
}

.mfp-s-ready .mfp-preloader {
  display: none;
}

.mfp-s-error .mfp-content {
  display: none;
}

button.mfp-close,
button.mfp-arrow {
  overflow: visible;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  display: block;
  outline: none;
  padding: 0;
  z-index: 1046;
  box-shadow: none;
  touch-action: manipulation;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.mfp-close {
  width: 44px;
  height: 44px;
  line-height: 44px;
  position: absolute;
  right: 0;
  top: 0;
  text-decoration: none;
  text-align: center;
  opacity: 0.65;
  padding: 0 0 18px 10px;
  color: #FFF;
  font-style: normal;
  font-size: 28px;
  font-family: Arial, Baskerville, monospace;
}

.mfp-close:hover,
.mfp-close:focus {
  opacity: 1;
}

.mfp-close:active {
  top: 1px;
}

.mfp-close-btn-in .mfp-close {
  color: #333;
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: #FFF;
  right: -6px;
  text-align: right;
  padding-right: 6px;
  width: 100%;
}

.mfp-counter {
  position: absolute;
  top: 0;
  right: 0;
  color: #CCC;
  font-size: 12px;
  line-height: 18px;
  white-space: nowrap;
}

.mfp-arrow {
  position: absolute;
  opacity: 0.65;
  margin: 0;
  top: 50%;
  margin-top: -55px;
  padding: 0;
  width: 90px;
  height: 110px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.mfp-arrow:active {
  margin-top: -54px;
}

.mfp-arrow:hover,
.mfp-arrow:focus {
  opacity: 1;
}

.mfp-arrow:before,
.mfp-arrow:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 0;
  margin-top: 35px;
  margin-left: 35px;
  border: medium inset transparent;
}

.mfp-arrow:after {
  border-top-width: 13px;
  border-bottom-width: 13px;
  top: 8px;
}

.mfp-arrow:before {
  border-top-width: 21px;
  border-bottom-width: 21px;
  opacity: 0.7;
}

.mfp-arrow-left {
  left: 0;
}

.mfp-arrow-left:after {
  border-right: 17px solid #FFF;
  margin-left: 31px;
}

.mfp-arrow-left:before {
  margin-left: 25px;
  border-right: 27px solid #3F3F3F;
}

.mfp-arrow-right {
  right: 0;
}

.mfp-arrow-right:after {
  border-left: 17px solid #FFF;
  margin-left: 39px;
}

.mfp-arrow-right:before {
  border-left: 27px solid #3F3F3F;
}

.mfp-iframe-holder {
  padding-top: 40px;
  padding-bottom: 40px;
}

.mfp-iframe-holder .mfp-content {
  line-height: 0;
  width: 100%;
  max-width: 900px;
}

.mfp-iframe-holder .mfp-close {
  top: -40px;
}

.mfp-iframe-scaler {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
}

.mfp-iframe-scaler iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #000;
}

/* Main image in popup */

img.mfp-img {
  width: auto;
  max-width: 100%;
  height: auto;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 40px 0 40px;
  margin: 0 auto;
}

/* The shadow behind the image */

.mfp-figure {
  line-height: 0;
}

.mfp-figure:after {
  content: '';
  position: absolute;
  left: 0;
  top: 40px;
  bottom: 40px;
  display: block;
  right: 0;
  width: auto;
  height: auto;
  z-index: -1;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #444;
}

.mfp-figure small {
  color: #BDBDBD;
  display: block;
  font-size: 12px;
  line-height: 14px;
}

.mfp-figure figure {
  margin: 0;
}

.mfp-bottom-bar {
  margin-top: -36px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  cursor: auto;
}

.mfp-title {
  text-align: left;
  line-height: 18px;
  color: #F3F3F3;
  word-wrap: break-word;
  padding-right: 36px;
}

.mfp-image-holder .mfp-content {
  max-width: 100%;
}

.mfp-gallery .mfp-image-holder .mfp-figure {
  cursor: pointer;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  background-color: #FAFBFC;
  color: #222222;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
}

h3,
h4,
h5,
h6 {
  font-size: 17px;
}

hr {
  height: 1px;
  border: none;
  margin: 20px 0;
  background-color: #999;
}

.main-content {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;
}

.embed-responsive::before {
  display: block;
  padding-top: 56.25%;
  content: "";
}

.embed-responsive__iframe {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.container {
  max-width: 1366px;
  padding: 0 43px;
  margin: 0 auto;
}

/* SITENAV */

.sitenav {
  padding: 10px 0;
  background-color: #fff;
}

.sitenav--fixed {
  position: fixed;
  z-index: 30;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}

.sitenav__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.sitenav--fixed .logo__img--symbol {
  display: none;
}

.sitenav--fixed .logo__img--text {
  display: block;
}

.sitenav__toggler {
  display: none;
}

.sitenav__collapse {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-grow: 1;
}

.sitenav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.sitenav__item + .sitenav__item {
  margin-left: 35px;
}

.sitenav__link {
  display: block;
  padding: 8px;
  text-decoration: none;
  color: #222222;
  transition: color 0.3s ease;
}

.sitenav__link:hover,
.sitenav__item--active .sitenav__link {
  color: #fab00b;
}

.sitenav__signup-link {
  display: block;
  padding: 8px;
  margin-left: auto;
  text-decoration: none;
  color: #fab00b;
  transition: color 0.3s ease;
}

.sitenav__signup-link:hover {
  color: #222222;
}

/* LOGO */

.logo {
  margin-right: 60px;
}

.logo__link {
  display: inline-block;
  vertical-align: middle;
}

.logo__img {
  display: block;
}

.logo__img--text {
  display: none;
}

.section {
  padding: 50px 0;
}

.section__heading {
  padding-left: 10px;
  border-left: 5px solid #fab00b;
  margin-top: 0;
  margin-bottom: 57px;
  font-size: 29px;
  line-height: 38px;
}

.section__footer {
  padding-top: 40px;
  text-align: center;
}

.section__footer-link {
  display: inline-block;
  vertical-align: middle;
  border-bottom: 1px solid #468aff;
  color: #468aff;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  transition: color 0.3s ease, border 0.3s ease;
}

.section__footer-link:hover {
  border-bottom-color: transparent;
}

.section__footer-link-icon {
  fill: #468aff;
}

.promo-slider__container {
  position: relative;
}

.promo-slider__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.promo-slider__item {
  position: relative;
}

.promo-slider__poster {
  border-radius: 8px;
  vertical-align: middle;
}

.promo-slider__item-inner {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.4);
}

.promo-slider__content {
  width: 50%;
  padding: 80px 0;
  padding-right: 40px;
  color: #fff;
}

.promo-slider__title {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 37px;
  line-height: 49px;
  text-shadow: 2px 2px 2px rgba(70, 41, 41, 0.3);
}

.promo-slider__text {
  margin-bottom: 20px;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
}

.promo-slider .tns-nav {
  position: absolute;
  left: 50%;
  bottom: 30px;
  transform: translateX(-50%);
  z-index: 10;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.promo-slider .tns-nav button {
  width: 30px;
  height: 7px;
  border: 1px solid #fab00b;
  margin: 0 10px;
  border-radius: 3px;
  background-color: #222;
}

.promo-slider .tns-nav .tns-nav-active {
  background-color: #fab00b;
}

.intro__container {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.intro__text-wrapper,
.intro__video-wrapper {
  width: 50%;
}

.intro__text-wrapper {
  padding: 0 100px;
}

.intro__heading {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 46px;
  line-height: 62px;
}

.intro__text {
  margin-top: 0;
}

.intro__video-wrapper {
  width: 50%;
}

.intro__video-wrapper::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../img/najot-talim-cover.jpg") no-repeat center center;
  content: "";
}

.intro__video-wrapper--playing::after {
  display: none;
}

.intro__play-button {
  position: absolute;
  z-index: 5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  padding: 0;
  border: 0;
  border-radius: 50%;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.16);
  cursor: pointer;
}

.intro__play-button svg {
  display: block;
  max-width: 100%;
  height: auto;
}

.intro__play-button:focus {
  outline: 0;
}

.intro__play-button:hover rect,
.intro__play-button:focus rect {
  fill: #fab00b;
}

.intro__play-button:hover path,
.intro__play-button:focus path {
  fill: #fff;
}

.intro__play-button:active {
  opacity: 0.9;
}

.intro__video-wrapper--playing .intro__play-button {
  display: none;
}

.button {
  display: inline-block;
  vertical-align: middle;
  padding: 10px;
  border: 0.5px solid #e4e4e4;
  border-radius: 4px;
  appearance: none;
  background: #fff;
  color: #000;
  font-size: 15px;
  line-height: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.button--open,
.button:hover,
.button:focus {
